  color: #c0c4cc;
  background-color: #252323;
}
.el-pagination .btn-prev,
.el-pagination .btn-next {
  background-size: 16px;
  background-color: #252323;
  color: #c0c4cc;
}
.el-pager li{
  background: #252323;
  background-color: #252323;
  color: #c0c4cc;
}
.el-pager >>> li {
  background: none;
  background-color: #252323;
  color: #c0c4cc;
}
.sharebonus {
  display: none;
}
.recommend-style {
  color: #dac28f;
  float: right;
}
</style>



